import React from 'react';
import { graphql } from 'gatsby';

// Components
import Layout from '../components/Layout/Layout';
import CollapseComp from '../components/CollapseComp/CollapseComp';
import QuestionComp from '../components/QuestionComp/QuestionComp';
import More from '../components/More/More';
export const query = graphql`
	{
		file(relativePath: { eq: "img/question.png" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
	}
`;
function QuestionsPage({ data }) {
	return (
		<Layout title="سوالات متداول">
			<div className="container">
				<QuestionComp img={data.file.childImageSharp.fluid} />
				<div className="d-flex justify-content-center mt-4" dir="rtl">
					<CollapseComp />
				</div>
				<div className="d-flex justify-content-center mt-5 mb-5">
					<More />
				</div>
			</div>
		</Layout>
	);
}

export default QuestionsPage;
