import React from 'react';
import { Collapse } from 'antd';
const { Panel } = Collapse;

function CollapseComp() {
	return (
		<Collapse style={{ width: '90%' }} defaultActiveKey={[ '1' ]}>
			<Panel style={{ textAlign: 'right', direction: 'rtl' }} header="نحوه خرید سم چگونه است؟" key="1">
				<p>
					جهت خرید سم وجه آن را به شماره کارت‌ و یا شماره حساب های درج شده در سایت واریز نمایید. جهت پرداخت به
					صورت کارت به کارت، می‌توانید از دستگاه های خودپرداز (عابر بانک) و یا کارت به کارت اینترنتی استفاده
					نمایید. چنانچه قیمت محصول را نمی دانید با ما تماس بگیرید. پس از واریز وجه حتماً با شماره های درج شده
					در سایت تماس بگیرید و واریزی را اطلاع دهید. لطفاً قبل و بعد از واریز وجه حتماً جهت هماهنگی با واحد
					فروش ۰۹۰۴۶۵۵۸۲۸۳ تماس حاصل نمایید.
				</p>
			</Panel>
			<Panel
				style={{ textAlign: 'right', direction: 'rtl' }}
				header="نحوه ارسال سم و هزینه ارسال پس از خرید سم چگونه است؟"
				key="2"
			>
				<p>
					نظر به اینکه ارسال سموم و مایعات جزو ممنوعات پستی اداره پست مرکزی ایران می باشد، از این رو کلیه
					ارسال ها بوسیله سیستم تیپاکس (و یا پایانه ترمینال) دقیقاً به آدرس شما انجام خواهد شد. هزینه ارسال
					کالا بر عهده فروشگاه ما و کاملاً رایگان خواهد بود جهت آموزش راهنمای استفاده از سم با شماره های درج
					شده در سایت تماس بگیرید و یا در واتس آپ – تلگرام از طریق شماره ۰۹۰۴۶۵۵۸۲۸۳ با ما در ارتباط باشید.
				</p>
			</Panel>
			<Panel
				style={{ textAlign: 'right', direction: 'rtl' }}
				header="شرح تضمین کیفیت کالا و گارانتی محصولات چگونه است؟"
				key="3"
			>
				<p>
					فروشگاه ایران کشت نوین اولین و تنها وارد کننده مستقیم سموم از شمال غرب کشور می باشد. کلیه سموم بصورت
					کاملاً وارداتی و بدون واسطه به دست مشتری خواهد رسید. برچسب سموم دارای هلوگرام + نشان برجسته شرکت
					سازنده + تاریخ برجسته تولید و انقضاء و بارکد اختصاصی می باشند، از این رو میتوانید با جستجوی بسیار
					ساده در اینترنت به اصالت کالا پی ببرید.
				</p>
			</Panel>
			<Panel
				style={{ textAlign: 'right', direction: 'rtl' }}
				header="مدت زمان ارسال و تحویل کالا به مشتری چقدر است؟"
				key="4"
			>
				<p>
					اطلاعات سفارشات مشتریان در همان روز سفارش (حتی روز های تعطیل) بر روی بسته ها درج می شود. سپس توسط
					سیستم کالارسانی شرکت تیپاکس به سرتاسر ایران ارسال می شوند. مدت زمان ارسال پس از خرید سم به تهران:
					کمتر از ۲۴ ساعت مدت زمان ارسال پس از خرید سم به شهرستان: حداکثر ۳۰ ساعت بعد از ثبت کالا در سیستم
					تیپاکس، کد پیگیری به شماره موبایل مشتری ارسال می شود. بنابراین در هر لحظه میتوانید با مراجعه به سایت
					تیپاکس (قسمت رهگیری مرسولات) از وضعیت سفارش خود مطلع شوید. چنانچه منطقه و شهر مورد سکونت شما فاقد
					شعبه های فعال تیپاکس باشند، بسته ها با سیستم باربری اتوبوسرانی در اسرع وقت، به ترمینال مرکزی شهر محل
					زندگیتان ارسال خواهد شد.
				</p>
			</Panel>
		</Collapse>
	);
}

export default CollapseComp;
